import Map from 'ol/Map.js';
import OSM from 'ol/source/OSM.js';
import TileLayer from 'ol/layer/WebGLTile.js';
import TileWMS from 'ol/source/TileWMS.js';
import View from 'ol/View.js';

const layers = [
  new TileLayer({
    source: new OSM(),
  }),
  new TileLayer({
    extent: [-12943199.5056647, 663000.65251361, -12836086.4158277, 6639629.24018357],
    source: new TileWMS({
      url: 'https://osgeo.cl.weblibre.ca/geoserver/cloud/osgeo/wms',
      params: {'LAYERS': 'osgeo:Canmore_Building_Footprints', 'TILED': true},
      serverType: 'geoserver',
      // Countries have transparency, so do not fade tiles:
      transition: 0,
    }),
  }),
];
const map = new Map({
  layers: layers,
  target: 'map',
  view: new View({
    center: [-12843199, 6638099],
    zoom: 14
    ,
  }),
});
